import React, { useState, useEffect, useRef, Fragment } from 'react'

function Active(props) {
  let {
    active,
    width,
    height,
    rows,
    cols,
    select_col,
    select_row,
    select_all,
    zoom_mode,
  } = props
  let vw = Math.floor(width / cols)
  let vh = Math.floor(height / rows)
  let ax = active % cols
  let ay = Math.floor(active / rows)
  let x = ax * vw
  let y = ay * vh

  let show_col = select_col && !select_all
  let show_row = select_row && !select_all
  let show_all = select_all

  let every = zoom_mode === 'every'
  let zoom_coords = []
  if (every) {
  } else {
    if (zoom_mode === 'canvas') {
      zoom_coords = [Math.floor(width / 2), Math.floor(height / 2)]
    } else if (zoom_mode === 'active') {
      zoom_coords = [x + vw / 2, y + vh / 2]
    }
  }

  let zoom_pointer_width = 8
  let zp = zoom_pointer_width / 2

  return (
    <Fragment>
      {show_col ? (
        <div
          style={{
            pointerEvents: 'none',
            position: 'fixed',
            left: x - 1,
            top: 0 - 1,
            width: vw + 2,
            height: height + 2,
            border: 'solid 2px rgba(160, 160, 160, 0.5)',
          }}
        ></div>
      ) : null}
      {show_row ? (
        <div
          style={{
            pointerEvents: 'none',
            position: 'fixed',
            left: 0 - 1,
            top: y - 1,
            width: width + 2,
            height: vh + 2,
            border: 'solid 2px rgba(160, 160, 160, 0.5)',
          }}
        ></div>
      ) : null}
      {show_all ? (
        <div
          style={{
            pointerEvents: 'none',
            position: 'fixed',
            left: 0 - 1,
            top: 0 - 1,
            width: width + 2,
            height: height + 2,
            border: 'solid 2px rgba(160, 160, 160, 0.5)',
          }}
        ></div>
      ) : null}
      <div
        style={{
          pointerEvents: 'none',
          position: 'fixed',
          left: x - 1,
          top: y - 1,
          width: vw + 2,
          height: vh + 2,
          border: 'solid 2px rgba(240, 240, 240, 0.5)',
        }}
      ></div>
      {every ? (
        <div>
          {show_all ? (
            [...Array(cols * rows)].map((_, i) => {
              let c = i % cols
              let r = Math.floor(i / cols)
              return (
                <div
                  className="zoom_pointer"
                  key={`every_${i}`}
                  style={{
                    left: c * vw + vw / 2 - zp,
                    top: r * vh + vh / 2 - zp,
                  }}
                ></div>
              )
            })
          ) : (
            <div
              className="zoom_pointer"
              style={{
                left: x + vw / 2 - zp,
                top: y + vh / 2 - zp,
              }}
            ></div>
          )}
          {show_col
            ? [...Array(rows)].map((_, r) => {
                if (r === ay) {
                  return null
                } else {
                  return (
                    <div
                      className="zoom_pointer"
                      key={`col_${r}`}
                      style={{
                        left: x + vw / 2 - zp,
                        top: r * vh + vh / 2 - zp,
                      }}
                    ></div>
                  )
                }
              })
            : null}
          {show_row
            ? [...Array(cols)].map((_, c) => {
                if (c === ax) {
                  return null
                } else {
                  return (
                    <div
                      className="zoom_pointer"
                      key={`row_${c}`}
                      style={{
                        left: c * vw + vw / 2 - zp,
                        top: y + vh / 2 - zp,
                      }}
                    ></div>
                  )
                }
              })
            : null}
        </div>
      ) : (
        <div
          className="zoom_pointer"
          style={{
            left: zoom_coords[0] - zp,

            top: zoom_coords[1] - zp,
          }}
        ></div>
      )}
    </Fragment>
  )
}

export default Active
