import React, { useState, useEffect, useRef, Fragment } from 'react'
import Gridlines from './Gridlines.js'
import { base_zoom } from '../u/Constants.js'
import {
  setViewports,
  initCameras,
  initImageMap,
  setProjectionMatrices,
  setViewMatrices,
  getWorldFromPx,
  mosaic,
} from '../u/gl_utils.js'
import { loadImage } from '../u/gl_actions.js'
import Mouse from '../handlers/Mouse.js'
import Active from './Active.js'

function Gl(props) {
  let $render_ref = useRef(null)
  let [loaded, setLoaded] = useState(false)
  let {
    width,
    height,
    scene_ref,
    rows,
    cols,
    select_row,
    select_col,
    select_all,
    zoom_mode,
    active,
    setActive,
  } = props
  let scene = scene_ref.current
  let $render = $render_ref.current

  useEffect(() => {
    setLoaded(true)

    let $render = $render_ref.current
    let regl = window.createREGL($render)
    scene.regl = regl
    scene.$render = $render

    scene.images = []
    scene.imageDraws = []
    setViewports(scene, $render, cols, rows)
    initImageMap(scene, cols, rows)
    initCameras(scene)
    setProjectionMatrices(scene)
    setViewMatrices(scene)

    scene.pixel = getWorldFromPx(scene, 1, base_zoom)

    let all_indexes = [...Array(cols * rows)].map((_, i) => i)

    loadImage(scene, '/images/keyboard.png', all_indexes)
    mosaic(scene, all_indexes)

    let gl = $render.getContext('webgl')
    gl.enable(gl.SCISSOR_TEST)

    regl.frame(({ tick }) => {
      regl.clear({
        color: [0, 0, 0, 1],
      })
      setViewMatrices(scene)
      let vy_offset = height - scene.viewports[0][3] * rows
      for (let i = 0; i < scene.viewports.length; i++) {
        let viewport = scene.viewports[i]
        let [vx, vy, vw, vh] = viewport
        // gap at bottom instead of top
        vy = vy + vy_offset
        gl.viewport(vx, vy, vw, vh)
        gl.scissor(vx, vy, vw, vh)
        regl.clear({
          color: [0.1, 0.1, 0.1, 1],
          depth: 1,
        })
        if (scene.viewport_image_map[i] !== null) {
          let draw = scene.imageDraws[scene.viewport_image_map[i]]
          draw({
            view_projection: scene.view_projections[i],
          })
        }
      }
    })
    return regl.destroy
  }, [])

  useEffect(() => {
    let $render = $render_ref.current
    setViewports(scene, $render, cols, rows)
    setProjectionMatrices(scene)
    // TODO special case add cols and rows
    // TODO compensate camera based on relation to center
  }, [cols, rows, width, height])

  // shadow state with scene
  let all_indexes = [...Array(cols * rows)].map((_, i) => i)
  let col = active % cols
  let row = Math.floor(active / cols)
  let inverted_row = rows - 1 - row
  scene.active = inverted_row * cols + col
  if (select_all) {
    scene.all_active = all_indexes
  } else {
    scene.all_active = [scene.active]
    if (select_col) {
      let col = scene.active % cols
      let col_array = []
      for (let r = 0; r < rows; r++) {
        let i = r * cols + col
        if (i !== scene.active) scene.all_active.push(i)
      }
    }
    if (select_row) {
      let row = Math.floor(scene.active / cols)
      for (let c = 0; c < cols; c++) {
        let i = row * cols + c
        if (i !== scene.active) scene.all_active.push(i)
      }
    }
  }
  scene.width = width
  scene.height = height
  scene.zoom_mode = zoom_mode

  return (
    <div
      style={{
        position: 'relative',
        width: width,
        height: height,
      }}
    >
      <canvas ref={$render_ref} width={width} height={height}></canvas>
      {loaded ? (
        <Fragment>
          <Gridlines rows={rows} cols={cols} width={width} height={height} />
          <Mouse
            $render={$render_ref.current}
            scene={scene}
            setActive={setActive}
          />
          <Active
            active={active}
            rows={rows}
            cols={cols}
            width={width}
            scene={scene}
            height={height}
            select_col={select_col}
            select_row={select_row}
            select_all={select_all}
            zoom_mode={zoom_mode}
          />
        </Fragment>
      ) : null}
    </div>
  )
}

export default Gl
