import React, { useState, useRef } from 'react'
import Sidebar from './components/Sidebar.js'
import Gl from './components/Gl.js'
import { sidebar_width } from './u/Constants.js'
import Keyboard from './handlers/Keyboard.js'
import useWindowSize from './u/useWindowSize.js'

function App() {
  let size = useWindowSize()
  let file_input = React.useRef(null)
  let [show_sidebar, setShowSidebar] = useState(true)
  let [cols, setCols] = useState(8)
  let [rows, setRows] = useState(8)
  let [active, setActive] = useState(0)
  let [select_row, setSelectRow] = useState(false)
  let [select_col, setSelectCol] = useState(false)
  let [select_all, setSelectAll] = useState(false)
  let [zoom_mode, setZoomMode] = useState('canvas')

  let addCol = () => {
    setCols((prev) => Math.min(16, prev + 1))
  }
  let remCol = () => {
    setCols((prev) => Math.max(1, prev - 1))
  }
  let addRow = () => {
    setRows((prev) => Math.min(16, prev + 1))
  }
  let remRow = () => {
    setRows((prev) => Math.max(1, prev - 1))
  }
  let toggleSelectRow = () => {
    setSelectRow((prev) => {
      if (prev === false) {
        setSelectAll(false)
      }
      return !prev
    })
  }
  let toggleSelectCol = () => {
    setSelectCol((prev) => {
      if (prev === false) {
        setSelectAll(false)
      }
      return !prev
    })
  }
  let toggleSelectAll = () => {
    setSelectAll((prev) => {
      if (prev === false) {
        setSelectCol(false)
        setSelectRow(false)
      }
      return !prev
    })
  }
  let clearSelections = () => {
    setSelectCol(false)
    setSelectRow(false)
    setSelectAll(false)
  }

  let scene_ref = React.useRef({
    active: 0,
    all_active: [0],
  })

  let setKeyboardActive = (dx, dy) => {
    setActive((prev) => {
      let cur_col = prev % cols
      let cur_row = Math.floor(prev / cols)
      let new_col = Math.max(0, Math.min(cols - 1, cur_col + dx))
      let new_row = Math.max(0, Math.min(rows - 1, cur_row + dy))
      return new_row * cols + new_col
    })
  }

  return (
    <React.Fragment>
      <input
        style={{ display: 'none' }}
        type="file"
        accept="image/*"
        ref={file_input}
      />
      {size.width && show_sidebar ? (
        <Sidebar
          scene={scene_ref.current}
          width={sidebar_width}
          cols={cols}
          rows={rows}
          select_row={select_row}
          select_col={select_col}
          select_all={select_all}
          zoom_mode={zoom_mode}
          addCol={addCol}
          addRow={addRow}
          remCol={remCol}
          remRow={remRow}
          toggleSelectAll={toggleSelectAll}
          toggleSelectRow={toggleSelectRow}
          toggleSelectCol={toggleSelectCol}
          clearSelections={clearSelections}
          setKeyboardActive={setKeyboardActive}
          setZoomMode={setZoomMode}
          file_input={file_input.current}
        />
      ) : null}

      {size.width ? (
        <Keyboard
          scene_ref={scene_ref}
          addCol={addCol}
          addRow={addRow}
          remCol={remCol}
          remRow={remRow}
          toggleSelectAll={toggleSelectAll}
          toggleSelectRow={toggleSelectRow}
          toggleSelectCol={toggleSelectCol}
          clearSelections={clearSelections}
          setZoomMode={setZoomMode}
          setKeyboardActive={setKeyboardActive}
          file_input={file_input.current}
        />
      ) : null}
      {size.width ? (
        <Gl
          width={size.width - sidebar_width}
          height={size.height}
          size={size}
          scene_ref={scene_ref}
          cols={cols}
          rows={rows}
          active={active}
          select_row={select_row}
          select_col={select_col}
          select_all={select_all}
          zoom_mode={zoom_mode}
          setZoomMode={setZoomMode}
          setActive={setActive}
        />
      ) : (
        <div>Loading...</div>
      )}
    </React.Fragment>
  )
}

export default App
