import React, { useState } from 'react'
import { KeyButton, ToggleKeyButton, KeyButtonOnce } from '../items/Buttons.js'
import {
  panSetPixels,
  zoomCenter,
  shake,
  viewportCenter,
  mosaic,
} from '../u/gl_utils.js'
import { domLoadImage } from '../u/gl_actions.js'

function Heading(props) {
  return <div style={{ marginBottom: 8 }}>{props.children.toUpperCase()}</div>
}

function Info(props) {
  return <div style={{ marginBottom: 8 }}>{props.children}</div>
}

function Spacer() {
  return <div style={{ width: '100%', height: 8 }}></div>
}

function HSpacer() {
  return <div style={{ width: '100%', height: 4 }}></div>
}

function Sidebar(props) {
  let {
    scene,
    width,
    rows,
    cols,
    addCol,
    addRow,
    remCol,
    remRow,
    select_row,
    select_col,
    select_all,
    toggleSelectAll,
    toggleSelectCol,
    toggleSelectRow,
    clearSelections,
    zoom_mode,
    setZoomMode,
    setKeyboardActive,
    file_input,
  } = props
  return (
    <div id="sidebar" style={{ width }}>
      <Heading>Selections</Heading>
      <div>
        <ToggleKeyButton
          text="Column"
          keyname="q"
          compare={select_col}
          click={toggleSelectCol}
        />
        <ToggleKeyButton
          text="Row"
          keyname="w"
          compare={select_row}
          click={toggleSelectRow}
        />
        <ToggleKeyButton
          text="All"
          keyname="e"
          compare={select_all}
          click={toggleSelectAll}
        />
        <KeyButton text="Clear" keyname="escape" click={clearSelections} />
      </div>
      <Spacer />
      <HSpacer />
      <Heading>Pan</Heading>
      <Info>Click and drag or use</Info>
      <div>
        <KeyButton
          text=""
          keyname="â†"
          click={panSetPixels.bind(null, scene, 1, 0)}
        />
        <KeyButton
          text=""
          keyname="â†“"
          click={panSetPixels.bind(null, scene, 0, 1)}
        />
        <KeyButton
          text=""
          keyname="â†‘"
          click={panSetPixels.bind(null, scene, 0, -1)}
        />
        <KeyButton
          text=""
          keyname="â†’"
          click={panSetPixels.bind(null, scene, -1, 0)}
        />
      </div>
      <Spacer />
      <HSpacer />
      <Heading>Zoom</Heading>
      <Info>Use mousewheel or</Info>
      <div>
        <KeyButton
          text="Zoom in"
          keyname="+"
          click={zoomCenter.bind(null, scene, 1)}
        />
        <KeyButton
          text="Zoom out"
          keyname="-"
          click={zoomCenter.bind(null, scene, -1)}
        />
      </div>
      <Spacer />
      <HSpacer />
      <Heading>Zoom center</Heading>
      <div>
        <ToggleKeyButton
          text="Canvas"
          keyname="z"
          compare={zoom_mode === 'canvas'}
          click={setZoomMode.bind(null, 'canvas')}
        />
        <ToggleKeyButton
          text="Active"
          keyname="x"
          compare={zoom_mode === 'active'}
          click={setZoomMode.bind(null, 'active')}
        />
        <ToggleKeyButton
          text="Every"
          keyname="c"
          compare={zoom_mode === 'every'}
          click={setZoomMode.bind(null, 'every')}
        />
      </div>
      <Spacer />
      <HSpacer />
      <Heading>Set Active</Heading>
      <Info>Move mouse, tap, or use</Info>
      <KeyButton
        text="â†"
        keyname="h"
        click={setKeyboardActive.bind(null, -1, 0)}
      />
      <KeyButton
        text="â†“"
        keyname="j"
        click={setKeyboardActive.bind(null, 0, 1)}
      />
      <KeyButton
        text="â†‘"
        keyname="k"
        click={setKeyboardActive.bind(null, 0, -1)}
      />
      <KeyButton
        text="â†’"
        keyname="l"
        click={setKeyboardActive.bind(null, 1, 0)}
      />
      <Spacer />
      <HSpacer />
      <Heading>Load image to selection</Heading>
      <KeyButtonOnce
        text="Load image"
        keyname="o"
        click={domLoadImage.bind(null, scene, file_input)}
      />
      <Spacer />
      <HSpacer />
      <Heading>More actions</Heading>
      <KeyButton text="Shake" keyname="t" click={shake.bind(null, scene)} />
      <KeyButton
        text="Center"
        keyname="y"
        click={viewportCenter.bind(null, scene)}
      />
      <KeyButton
        text="Relative Center"
        keyname="u"
        click={mosaic.bind(null, scene, scene.all_active)}
      />
      <Spacer />
      <HSpacer />
      <Heading>Size</Heading>
      <div
        style={{
          display: 'flex',
          alignItems: 'center',
        }}
      >
        <div style={{ marginBottom: 8, marginRight: 8 }}>Cols:</div>
        <div>
          <KeyButton text="-" keyname="f" click={remCol} />
          <KeyButton text="+" keyname="g" click={addCol} />
        </div>
        <div style={{ marginBottom: 8 }}>{cols}</div>
      </div>
      <div
        style={{
          display: 'flex',
          alignItems: 'center',
        }}
      >
        <div style={{ marginBottom: 8, marginRight: 8 }}>Rows:</div>
        <div>
          <KeyButton text="-" keyname="v" click={remRow} />
          <KeyButton text="+" keyname="b" click={addRow} />
        </div>
        <div style={{ marginBottom: 8 }}>{rows}</div>
      </div>
    </div>
  )
}

export default Sidebar
