import React, { useState, useEffect, useRef } from 'react'
import { panViewports, zoomCenter } from '../u/gl_utils.js'

function Mouse(props) {
  let { $render, scene, active, setActive } = props
  let mouse_ref = useRef({
    raw: [0, 0],
    down: false,
  })

  useEffect(() => {
    let mouse = mouse_ref.current

    function getActiveFromMouse(mouse_raw) {
      let [mx, my] = mouse_raw
      for (let i = 0; i < scene.viewports.length; i++) {
        let viewport = scene.viewports[i]
        let [x, y, w, h] = viewport
        if (mx >= x && mx <= x + w && my >= y && my <= y + h) {
          return i
        }
      }
    }

    function mouseMove(e) {
      let raw = [e.clientX, e.clientY]
      // assume container is at left bottom
      if (mouse.down) {
        let dx = raw[0] - mouse.raw[0]
        let dy = raw[1] - mouse.raw[1]
        panViewports(scene, dx, dy)
      } else {
        let mactive = getActiveFromMouse(mouse.raw)
        if (mactive !== undefined && mactive !== active) {
          setActive(mactive)
        }
      }
      mouse.raw = raw
    }

    function mouseDown(e) {
      mouse.down = [e.clientX, e.clientY]
      $render.setPointerCapture(e.pointerId)
    }

    function mouseUp(e) {
      mouse.down = null
      let mactive = getActiveFromMouse(mouse.raw)
      if (mactive !== undefined && mactive !== active) {
        setActive(mactive)
      }
      $render.releasePointerCapture(e.pointerId)
    }

    function mouseWheel(e) {
      e.preventDefault()

      let sign = e.deltaY < 0 ? -1 : 1
      zoomCenter(scene, sign)
    }

    $render.addEventListener('mousemove', mouseMove)
    $render.addEventListener('pointerdown', mouseDown)
    $render.addEventListener('pointerup', mouseUp)
    $render.addEventListener('wheel', mouseWheel, { passive: false })
    return () => {
      $render.removeEventListener('mousemove', mouseMove)
      $render.removeEventListener('pointerdown', mouseDown)
      $render.removeEventListener('pointerup', mouseDown)
      $render.removeEventListener('wheel', mouseWheel)
    }
  }, [])

  return null
}

export default Mouse
