import React, { useEffect, useState, useRef } from 'react'
import {
  panSetPixels,
  zoomCenter,
  shake,
  viewportCenter,
  mosaic,
} from '../u/gl_utils.js'
import { domLoadImage } from '../u/gl_actions.js'

function Keyboard(props) {
  let km_ref = useRef({})
  let scene = props.scene_ref.current
  let {
    addCol,
    addRow,
    remCol,
    remRow,
    toggleSelectCol,
    toggleSelectRow,
    toggleSelectAll,
    clearSelections,
    setZoomMode,
    setKeyboardActive,
    file_input,
  } = props

  function act(condition, action) {
    let km = km_ref.current
    if (km[condition]) {
      action()
    }
  }

  function actOnce(key, condition, action) {
    if (key === condition) {
      action()
    }
  }

  useEffect(() => {
    function keyAction(key, e) {
      // change selections
      act('q', toggleSelectCol)
      act('w', toggleSelectRow)
      act('e', toggleSelectAll)
      act('escape', clearSelections)

      // pan active
      act('arrowleft', panSetPixels.bind(null, scene, 1, 0))
      act('arrowright', panSetPixels.bind(null, scene, -1, 0))
      act('arrowup', panSetPixels.bind(null, scene, 0, -1))
      act('arrowdown', panSetPixels.bind(null, scene, 0, 1))

      // zoom
      act('+', zoomCenter.bind(null, scene, -1))
      act('=', zoomCenter.bind(null, scene, -1))
      act('-', zoomCenter.bind(null, scene, 1))

      // zoom mode
      act('z', setZoomMode.bind(null, 'canvas'))
      act('x', setZoomMode.bind(null, 'active'))
      act('c', setZoomMode.bind(null, 'every'))

      // zoom active
      act('h', setKeyboardActive.bind(null, -1, 0))
      act('j', setKeyboardActive.bind(null, 0, 1))
      act('k', setKeyboardActive.bind(null, 0, -1))
      act('l', setKeyboardActive.bind(null, 1, 0))

      // load image
      actOnce(key, 'o', domLoadImage.bind(null, scene, file_input))

      // more actions
      act('t', shake.bind(null, scene))
      act('y', viewportCenter.bind(null, scene))
      act('u', mosaic.bind(null, scene, scene.all_active))

      // add/rem rows and cols
      act('g', addCol)
      act('f', remCol)
      act('b', addRow)
      act('v', remRow)
    }

    function downHandler(e) {
      let km = km_ref.current
      let key = e.key.toLowerCase()
      km[key] = true
      keyAction(key, e)
    }

    function upHandler(e) {
      let km = km_ref.current
      let key = e.key.toLowerCase()
      km[key] = false
    }

    window.addEventListener('keydown', downHandler)
    window.addEventListener('keyup', upHandler)
    return () => {
      window.removeEventListener('keydown', downHandler)
      window.removeEventListener('keyup', upHandler)
    }
  }, [])
  return null
}

export default Keyboard
