import { initImageQuad } from './shaders.js'

export function loadImage(scene, src, indexes) {
  let $img = new Image()
  $img.onload = function () {
    let w = $img.width * scene.pixel
    let h = $img.height * scene.pixel

    let x0 = -w / 2
    let y0 = h / 2
    let x1 = w / 2
    let y1 = -h / 2
    let place = [x0, y0, x1, y1]

    let image_index = scene.images.length
    scene.images.push($img)
    let drawImage = initImageQuad(scene, $img, ...place)
    scene.imageDraws.push(drawImage)

    for (let i = 0; i < indexes.length; i++) {
      let index = indexes[i]
      scene.viewport_image_map[index] = image_index
    }
  }
  $img.src = src
}

export function domLoadImage(scene, input) {
  function handleChange(e) {
    let files = ''
    for (let item of this.files) {
      files += item.name + '.' + item.type
      if (item.type.indexOf('image') < 0) {
        continue
      }
      let src = URL.createObjectURL(item)
      loadImage(scene, src, scene.all_active)
      input.value = null
    }
    input.removeEventListener('change', handleChange)
  }
  input.addEventListener('change', handleChange)

  input.dispatchEvent(
    new MouseEvent('click', {
      bubbles: true,
      cancelable: true,
      view: window,
    })
  )
}


