import React, { useState, useEffect, useRef } from 'react'

function Gridlines(props) {
  let { cols, rows, width, height } = props
  let vw = Math.floor(width / cols)
  let vh = Math.floor(height / rows)
  return (
    <div>
      {cols > 0
        ? [...Array(cols - 1)].map((_, c) => {
            return (
              <div
                key={`col_${c}`}
                style={{
                  pointerEvents: 'none',
                  position: 'absolute',
                  left: (c + 1) * vw - 1,
                  top: 0,
                  width: 2,
                  height: '100%',
                  background: 'rgba(100,100,100,0.5)',
                }}
              ></div>
            )
          })
        : null}

      {rows > 0
        ? [...Array(rows - 1)].map((_, r) => {
            return (
              <div
                key={`row_${r}`}
                style={{
                  pointerEvents: 'none',
                  position: 'absolute',
                  left: 0,
                  top: (r + 1) * vh - 1,
                  height: 2,
                  width: '100%',
                  background: 'rgba(100,100,100,0.5)',
                }}
              ></div>
            )
          })
        : null}
    </div>
  )
}

export default Gridlines
